@mixin left {
  left: 20%;
  transform: translateX(-50%) rotate(-30deg);
}
@mixin right {
  left: 80%;
  transform: translateX(-50%) rotate(30deg);
}
@mixin center {
  left: 50%;
  transform: translateX(-50%);
}
@mixin f-left {
  left: 30%;
  transform: translateX(-50%) rotate(-12deg);
}

@mixin f-right {
  left: 70%;
  transform: translateX(-50%) rotate(12deg);
}
.draw-component-wrapper {
  width: 90%;
  height: 60vh;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  & .plane {
    position: relative;
    width: 100%;
    height: 100%;
    background: #e9e9e9;
    border: 2px dotted #2bbbad;
