      & .balloon {
        //max-height: 100px;

        position: absolute;
        //width: 50px;
        left: 50%;
        transform: translateX(-50%);
        &:hover {
          z-index: 20 !important;
        }
        & .balloon-img {
          transition: all 0.3s ease-in-out;
          opacity: 0.9;
          &:hover {
            transform: scale(1.09);
            opacity: 1;
          }
        }
        & svg {
          transition: all 0.3s ease-in-out;

          opacity: 0.9;

          &:hover {
            transform: scale(1.09);
            opacity: 1;
          }
        }
      }
    }
